.mainContainer {
  position: relative;
  column-gap: 26px;
  width: 515px;
  height: 215px;
  padding: 16px 50px 16px 26px;
  border-radius: 20px;
  background-color: #f7f7f5;
}
.mianInfo {
  row-gap: 23px;
  height: 140px;
  padding: 14px 70px 17px 18px;
  border-left: 1px solid #d9d9d9;
}
.minus {
  position: absolute;
  cursor: pointer;
  right: 10px;
  bottom: 10px;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  font-weight: 800;
  font-size: 50px;

  & img {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
